.header {
    width: 100%;
    height: 50px;
    padding: 0 19px;
    background: #0094FF;
    display: flex;
    align-items: center;
    box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.header img {
    width: 30px;
    height: 30px;
}

.title {
    font-size: 24px;
    line-height: 28px;
    margin-left: 12px;
}

@media (max-width: 900px) {
    .burger {
        margin-left: 20px;
        width: 20px;
        height: 20px;
        position: relative;
        transition: 1s all ease;
    }

    .burger span {
        height: 2px;
        display: block;
        background-color: black;
        transition: 1s all ease;
    }

    .burger span::before,
    .burger span::after {
        content: ' ';
        display: block;
        height: 2px;
        background-color: black;
        position: relative;
        left: 0;
        top: 0px;
        transition: 0.5s all ease;
    }

    .burger span::before {
        top: -5px;
    }

    .burger span::after {
        top: 3px;
    }


    .burger.active span {
        transform: scale(1);

    }

    .burger.active span::before {
        width: 50%;
        top: -4px;
        left: 0;
        transform: rotate(-45deg);
    }

    .burger.active span::after {
        width: 50%;
        top: 2px;
        transform: rotate(45deg);
    }

}